<template>
	<view>
		
		<view v-if="list.state==2" style="width: 100vw;height: 10vw;" class="hengfang">
			<view style="width: 2vw;height: 6vw;background: #39B54A;margin-left: 2vw;margin-top: 2vw;"></view>
			<view style="line-height: 10vw;margin-left: 2vw;">已处理</view>
		</view>
		<view v-if="list.state==0" style="width: 100vw;height: 10vw;" class="hengfang">
			<view style="width: 2vw;height: 6vw;background: #b3b4b5;margin-left: 2vw;margin-top: 2vw;"></view>
			<view style="line-height: 10vw;margin-left: 2vw;">未处理</view>
		</view>
		<view v-if="list.state==1" style="width: 100vw;height: 10vw;" class="hengfang">
			<view style="width: 2vw;height: 6vw;background: #e7e717;margin-left: 2vw;margin-top: 2vw;"></view>
			<view style="line-height: 10vw;margin-left: 2vw;">处理中</view>
		</view>
		
		
		<view style="background: #FFFFFF;width: 100vw;min-height: calc(100vh - 10vw);font-size: 120%;">
			<view class="hengfang" style="width: inherit;padding-top: 3vw;padding-bottom: 0vw;">
				<view style="flex: 1;margin-left: 4vw;color: #c3c3c3;">反馈标题</view>
				<view style="flex: 2;padding-right: 2vw;">{{list.feedbackTitle}}</view>
			</view>
			<view v-if="list.state==2" class="hengfang" style="width: inherit;padding-top: 3vw;padding-bottom: 0vw;">
				<view style="flex: 1;margin-left: 4vw;color: #c3c3c3;">管理回馈</view>
				<view style="flex: 2;padding-right: 2vw;">{{list.resultMsg}}</view>
			</view>
			<view class="hengfang" style="width: inherit;padding-top: 3vw;padding-bottom: 0vw;">
				<view style="flex: 1;margin-left: 4vw;color: #c3c3c3;">联系人</view>
				<view style="flex: 2;">{{list.customerName}}</view>
			</view>
			<view class="hengfang" style="width: inherit;padding-top: 3vw;padding-bottom: 0vw;">
				<view style="flex: 1;margin-left: 4vw;color: #c3c3c3;">联系手机</view>
				<view style="flex: 2;">{{list.mobilePhone}}</view>
			</view>
			<view class="hengfang" style="width: inherit;padding-top: 3vw;padding-bottom: 0vw;">
				<view style="flex: 1;margin-left: 4vw;color: #c3c3c3;">反馈描述</view>
				<view style="flex: 2;padding-right: 2vw;">
				{{list.feedbackMsg}}
				</view>
			</view>
			<view class="hengfang" style="width: inherit;padding-top: 3vw;padding-bottom: 0vw;">
				<view style="flex: 1;margin-left: 4vw;color: #c3c3c3;">反馈图片</view>
				<view style="flex: 2;">
					<image v-for="(item,index) in list.photoList" :key="index" :src="item" mode="aspectFit" style="width: 60vw;margin-top: 1vw;"></image>
				</view>
			</view>
		</view>
		
		
	</view>
</template>

<script>
	export default {
		data() {
			return {
				feedbackId:"",
				list:{},
				
			}
		},
		methods: {
			huoqu_fankui_xinxi:function(){
				let _this = this
				let data = {
					feedbackId:_this.feedbackId,
				}
				_this.wangluo_qingqiu('/wx/ipms/getFeedbackDetails', data, true, true, 'GET').then(value => {
					if (value) {
						_this.list = value
					}
				})
				
			}
		},
		onLoad:function(res){
			// console.log(res)
			
			this.feedbackId = res.id
			this.huoqu_fankui_xinxi()
		}
	}
</script>

